.item {
    height: 144px;
    display: flex;
    color: black;
    text-decoration: none;
    text-align: start;
    border: 1px solid;
    align-items: center;
    width: 70%;
    margin: auto;
    border: none;
    justify-content: space-around;
}

.item__image {
    width: 144px;
    height: 100%;
    border-radius: 2%;
}

.item__description {
    width: 25%;
}

.item__price {
    color: #009845;
    font-size: 24px;
    line-height: 144px;
    margin: 0;
}

.item__quantity {
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 210px;
}

.item__count {
    margin: 0;
    height: 28px;
    width: 28px;
    padding: 0;
    background-color: white;
    border: none;
    border-radius: 50%;
}

.item__value {
    width: 40px;
    height: 24px;
}

.option {
    margin: 0 auto;
    width: 70%;
    display: grid;
    grid-template-rows: 15% 85%;
}

.option__title {
    font-size: 32px;
    text-align: start;
}

.option__list {
    margin: 0;
    padding: 0;
}

.item__button {
    width: 160px;
    height: 54px;
    font-size: 20px;
    background-color: #009845;
    border: none;
    color: white;
    justify-self: center;
}